<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>基本资料</span>
    </div>
    <!-- 表单 -->
    <el-form
     :model="userForm"
     :rules="userRules"
     ref='userForm'
     label-width="80px">
  <el-form-item label="登录名称">
    <el-input disabled v-model="userForm.username"></el-input>
  </el-form-item>

   <el-form-item label="用户昵称" prop="nickname">
    <el-input v-model="userForm.nickname"></el-input>
  </el-form-item>

   <el-form-item label="用户邮箱" prop="email">
    <el-input v-model="userForm.email"></el-input>
  </el-form-item>

  <el-form-item>
      <el-button @click="submitHandler" type="primary"> 提交按钮 </el-button>
      <el-button @click="resetHandler" type="danger"> 重置 </el-button>
  </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  name: 'UserInfo',
  methods: {
    resetHandler () {
    //   this.$refs.userForm.resetFields()
      this.userForm = { ...this.$store.state.userInfo }
      this.$refs.userForm.clearValidate()
    },
    submitHandler () {
      this.$refs.userForm.validate(async result => {
        if (!result) return
        await this.$http.put('/my/userinfo', this.userForm)
        // console.log(res)

        // if (res.code !== 0) return this.$message.error(res.message)
        // this.$message.success(res.message)
        this.$store.dispatch('getUserInfo')
      })
    }
  },
  data () {
    return {
      // 利用浅拷贝来渲染用户的信息
    //   userForm: Object.assign({}, this.$store.state.userInfo),
    // 利用展开运算符实现浅拷贝
      userForm: { ...this.$store.state.userInfo },
      userRules: {
        nickname: [
          { required: true, message: '请输入用户昵称', trigger: 'blur' },
          { pattern: /^\S{1,10}$/, message: '昵称必须是1-10位的非空字符串', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入用户邮箱', trigger: 'blur' },
          { type: 'email', message: '邮箱格式不正确', trigger: 'blur' }
        ]
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-form {
  width: 500px;
}
</style>
